import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';

import {AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument} from 'angularfire2/firestore';
import {Observable} from 'rxjs';
import {Answer} from '../shared/answer.model';

interface Question {
  type: string;
  question: string;
  music: string;
  colors: string[];
}

@Component({
  selector: 'app-questions',
  templateUrl: './questions.component.html',
  styleUrls: ['./questions.component.css']
})
export class QuestionsComponent implements OnInit {

  questionCols: AngularFirestoreCollection<Question>;
  questions: Observable<Question[]>;

  answers: string[];

  current: number;
  max = 0;

  constructor(public router: Router, private afs: AngularFirestore) {
  }

  ngOnInit() {
    this.current = 0;
    this.questionCols = this.afs.collection('Questions');
    this.questions = this.questionCols.valueChanges();
    this.answers = new Array();
    this.questions.subscribe(val => this.max = val.length);
  }

  onButton(color: string) {
    this.answers.push(color)
    if (this.current < this.max - 1) {
      this.current++;
    } else {
      this.uploadAnswers();
      this.router.navigateByUrl('/finish');
    }
  }

  uploadAnswers() {
    this.afs.collection('Answers').add({'answers': this.answers});
  }

}
